.nav-console {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 5vh;
    z-index: 999;
}

.nav-wrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 6vh;
    background-color: black;
    opacity: 80%;
}

.nav-wrapper ul {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    list-style-type: none;
    color: whitesmoke;
    font-family: 'Roboto Condensed', sans-serif;
}

.nav-wrapper li {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    line-break: strict;
    word-wrap: unset;
    cursor: pointer;
}

.nav-wrapper h1 {
    padding-left: 1%;
    width: 30%;
    color: whitesmoke;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 700;
    font-size: 1.5em;
    cursor: pointer;
}

.nav-wrapper i {
    color: white;
    font-size: 1.5em;
    visibility: hidden;
    padding-right: 5%;
}


/*====MOBILE====*/
@media only screen and (max-width: 950px) { 
    .nav-wrapper i {
        visibility: visible;
    }
    
    .nav-wrapper h1 {
        width: 80%;
        font-size: 1em;
    }

    .nav-wrapper li {
        visibility: hidden;
    }
    
}
