.about-console {
    position: absolute;
    top: 200vh;
    width: 100%;
    height: 100vh;
    background-color: red;
}

.about-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.about-images:nth-child(1) {

}
.about-images img {
    width: 100%;
    max-width: 17em;
    border: 2px solid whitesmoke;
}






/*====MOBILE====*/
@media only screen and (max-width: 1200px) { 
    .about-console {
        top: 250vh;
    }
}  


@media only screen and (max-width: 950px) { 
    .about-console {
        top: 280vh;
    }
}